import React from 'react'
import { View, Text , StyleSheet, Dimensions, Image} from 'react-native'

export default () => {
    return (
        <View style={styles.container}>
            <Image
          source={require('../Header/034-Alberth (2).JPG')}
          style={styles.image1}
        />
            <Text style={styles.title}>Alberth Christadi</Text>
        </View>
    )
}

const {height, width} = Dimensions.get('window')

const styles = StyleSheet.create({
    container: {
        alignSelf: 'stretch',
        height: height/10,
        backgroundColor: '#333',
        justifyContent: 'flex-start ',
        padding: 11,
        flexDirection: 'row'
    },
    title:{
        color: 'white',
        fontSize: 30,
        fontWeight: 'bold'
    },
    image1:{
     height: 50,
     width: 50,
     borderRadius: 25,
     marginRight: 5
    }

})